<template>
	<div :id="mapId" class="map__item">
		<div class="map__render">
			<img :src="require('@/assets/images/map-' + mapId +'.png')" :alt="'Map render ' + mapId">
		</div>
		<ul class="map__markers">
			<li class="marker" v-for="marker in mapPoints"
				:key="'marker' + marker.id"
				:class="{'marker-hover': marker.isHovering,'marker-active': marker.isActive}"
				:style="{top: marker.top + '%', left: marker.left + '%'}"
				@mouseover="marker.isHovering = true"
				@mouseout="marker.isHovering = false">
				<div class="marker__wrapper" v-if="activeCategory == 0 || marker.categoryID == activeCategory"  :key="'markerWrapper' + marker.id">
					<button class="btn btn_marker"
							type="button"
							:style="{background: categoriesColors[marker.categoryID]}"
							@click="openContent(marker.id)">
						<span class="title" v-if="mapPointsContent[marker.id].length > 1">{{ allCategories[marker.categoryID].name }}</span>
						<span class="title" v-else>{{ mapPointsContent[marker.id].name }}</span>
						<img v-if="marker.categoryID < 5" :src="require('@/assets/images/section-' + marker.categoryID +'.svg')" :alt="categories[marker.categoryID].name">
						<img v-else :src="require('@/assets/images/photo.svg')" :alt="allCategories[marker.categoryID].name">
						<span class="count" v-if="mapPointsContent[marker.id].length > 1">{{mapPointsContent[marker.id].length}}</span>
					</button>
					<div class="marker__content" v-if="mapPointsContent[marker.id].length > 1">
						<ul class="content__list">
							<li class="content__item"
								v-for="(item, index) in mapPointsContent[marker.id]"
								:key="marker.id + 'item' + index"
								@click="$parent.initVideo(item)">
								<h3 class="content__title">{{ item.name }}</h3>
								<div class="content__marker" :style="{background: categoriesColors[marker.categoryID]}">{{ allCategories[marker.categoryID].name }}</div>
								<button class="content__play" type="button">
									<img :src="require('@/assets/images/play.svg')" alt="Воспроизвести" v-if="marker.categoryID < 5" />
									<img :src="require('@/assets/images/eye.svg')" alt="Смотреть" v-else />
								</button>
								<div class="content__preview">
									<video :alt="item.name" v-if="marker.categoryID < 5">
										<source :src="item.url" :type="'video/' + item.extension" />
									</video>
									<img :src="item.url" :alt="item.name" v-else />
								</div>
							</li>
						</ul>
					</div>
					<div class="marker__content marker__content_single" v-else >
						<div class="content__preview content__preview_single">
							<video :alt="mapPointsContent[marker.id].name" v-if="marker.categoryID < 5">
								<source :src="mapPointsContent[marker.id].url" :type="'video/' + mapPointsContent[marker.id].extension" />
							</video>
							<img :src="mapPointsContent[marker.id].url" :alt="mapPointsContent[marker.id].name" v-else />
							<button class="content__play content__play_single" type="button" @click="$parent.initVideo(mapPointsContent[marker.id])">
								<img :src="require('@/assets/images/play.svg')" alt="Воспроизвести" v-if="marker.categoryID < 5" />
								<img :src="require('@/assets/images/eye.svg')" alt="Смотреть" v-else />
							</button>
						</div>
						<h3 class="content__title content__title_single">{{ mapPointsContent[marker.id].name }}</h3>
						<p class="content__category content__category_single">{{ allCategories[marker.categoryID].name }}</p>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */
import { mapGetters } from 'vuex'

// const WARNING_MAP = {}

export default {
	name: 'MapItem',
	props: ['mapId', 'map', 'categories', 'activeCategory', 'categoriesColors', 'mapPoints'],
	data() {
		return {
		}
	},
	computed: {
		...mapGetters(['allContent', 'sections', 'panoramas', 'videos', 'videoCategories', 'allCategories']),
		mapPointsContent() {
			let pointsArray = []
			pointsArray.push(this.videoCategories[3][0])
			pointsArray.push(this.panoramas[0])
			pointsArray.push(this.videoCategories[2][0])
			pointsArray.push(this.videoCategories[0][0])
			pointsArray.push(this.videoCategories[0][1])
			let a = []
			for (let i = 1; i < 4; i++) {
				a.push(this.videoCategories[2][i])
			}
			pointsArray.push(a)
			pointsArray.push(this.panoramas[1])
			let b = []
			for (let i = 1; i < 4; i++) {
				b.push(this.videoCategories[3][i])
			}
			pointsArray.push(b)
			let c = []
			for (let i = 1; i < 4; i++) {
				c.push(this.videoCategories[1][i])
			}
			pointsArray.push(c)
			pointsArray.push(this.panoramas[2])
			pointsArray.push(this.videoCategories[0][2])
			pointsArray.push(this.videoCategories[3][3])
			pointsArray.push(this.videoCategories[0][3])
			return pointsArray
		}
	},
	methods: {
		openContent(id) {
			let activePointID = this.mapPoints.filter((e) => e.isActive === true)
			if (activePointID.length > 0) {
				this.mapPoints[activePointID[0].id].isActive = false
				this.mapPoints[activePointID[0].id].isVisited = true
				this.mapPoints[id].isHovering = false
			}
			this.mapPoints[id].isActive = true
			this.mapPoints[id].isHovering = true
			if (activePointID.length > 0 && activePointID[0].id === id) {
				this.mapPoints[id].isActive = false
				this.mapPoints[id].isVisited = true
				this.mapPoints[id].isHovering = false
			}
		}
	}
}
</script>

<style lang="scss"></style>
